const data = [];

const getAllCourse = [
  {
    url: `/course`,
    type: 'get',
    response: (config) => {
      const obj = config.query;
      if (obj.method === 'getall') {
        return {
          code: 200,
          msg: 'success',
          data: AllData,
        };
      } else if (obj.method === 'getone') {
        const oneData = AllData.filter((item) => item.stuid === obj.index);
        return {
          code: 200,
          msg: 'success',
          data: oneData,
        };
      }
    },
  },
];

const addCourse = [
  {
    url: `/course`,
    type: 'post',
    response() {
      return {
        code: 200,
        msg: 'success',
        data,
      };
    },
  },
];
module.exports = { getAllCourse, addCourse };
